* {
  padding:0;
  margin:0;
  box-sizing:border-box;
}

.container-fluid{
  background-color:rgb(177, 177, 243);
  }

.content{
  height: 110vh;
}


.register-page{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 75vh;

}

.register-image img{
  width:500px;
  height: 300px;
  margin-left: 150px;
  }

.register-form {
    margin-top: 200px;
    margin-bottom: 50px;
    padding: 100px; /* Add padding to the form */
    margin-right: 100px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
   }


.login-page{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 75vh;
  }

 .login-image img{
  width:500px;
  height: 300px;
  margin-left: 150px;
  }

.login-form {
    margin-top: 190px;
    margin-bottom: 50px;
    width: 40%;
    height: 120%;
    padding: 100px; /* Add padding to the form */
    margin-right: 100px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
   }

/* transaction page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* antd icons */
.anticon svg{
  font-size:20px;
  cursor:pointer;
}

.switch-icons{
  border: 1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.active-icon{
  color:black;
}

.inactive-icon{
  color:gray;
}


